@import url(https://fonts.googleapis.com/css2?family=Lato&family=Nunito+Sans:wght@800&display=swap);
@font-face {
  font-family: "Futura Medium";
  font-style: normal;
  src: local("futura-medium"),
    url(/static/media/futura-medium.9a162627.ttf) format("ttf");
}

header {
  font-family: "Nunito Sans", sans-serif;
  font-weight: regular;
}

h1 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: regular;
}

h2 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: regular;
  font-size: 36px;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-weight: regular;
  font-size: 19px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

